@use 'base/variables' as v;

.no-webp .image-contact{
    background-image: url('../../../build/img/encuentra.jpg');
}

.webp .image-contact{
    background-image: url('../../../build/img/encuentra.webp');
}

.image-contact{
background-position: center center;
background-size: cover;
display: flex;
flex-direction: column;
align-items: center;

h2{
    font-size: 4rem;
    font-weight: v.$black;
    color: v.$blanco;
}

p{
    font-size: 1.8rem;
    color: v.$blanco;
}
}