@use 'base/variables' as v;
@use 'base/mixins' as m;
.no-webp .start{
    background-image: url('../../../build/img/header.jpg')
}
.webp .start{
    background-image: url('../../../build/img/header.webp');
}
.header{
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0;

    &.start{
        background-position: center center;
        background-size: cover;
        @include m.tablet{
            min-height: 70rem;
        }
        .contents-header{
            @include m.tablet {
                min-height: 70rem;
            }
        }
    }
}

.contents-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

  
    h1{
        text-align: left;
        color: v.$blanco;
        font-weight: v.$bold;
        line-height: 2;
        max-width: 60rem;
    }
}